a {
  color: #333;
  text-decoration: none;
}

button {
  background-color: #333;
  border: 0;
  border-radius: 6px;
  color: white;
  font-weight: bold;
  padding: 10px 20px;
}

input[type="text"], label, textarea {
  display: block;
}

input[type="text"], textarea {
  border: 1px solid #333;
  border-radius: 4px;
  margin: 10px 0 20px 0;
  padding: 10px;
  width: 400px;
}

label {
  font-weight: bold;
}

.article-list-item {
  border-bottom: 3px solid #333;
  padding: 20px 0;
}

.page-body {
  line-height: 1.5;
  margin: 60px auto;
  max-width: 800px;
}
