nav a {
    display: block;
    font-weight: bold;
    padding: 20px 40px;
}

nav a:hover {
    background-color:#333;
    color: white;
}

nav {
    border-bottom: 5px solid #333;
}

nav ul {
    display: flex;
    justify-content: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
}